import React, {Component} from "react";
import "../style/ItemEdit.css";


export default class ItemEdit extends Component{
    constructor(props){
        super(props);

        this.state={
            "title":this.props.item.title,
            "pic":this.props.item.pic,
            "description":this.props.item.description,
            "amount":this.props.item.amount,
            "stateColor":this.props.item.stateColor,
            "key":this.props.item.key,
            "showEdit":this.props.item.showEdit,
            "price":this.props.item.price
        };

        this.updateValues = this.updateValues.bind(this);
    }

    updateValues(e){
        let elm = e.target;

        if(elm.name === "title"){
            this.setState({"title":elm.value});
        }
        else if(elm.name === "pic"){
            this.setState({"pic":elm.value});
        }
        else if(elm.name === "description"){
            this.setState({"description":elm.value});
        }
        else if(elm.name === "amount"){
            this.setState({"amount":elm.value});
        }
        else if(elm.name === "price"){
            this.setState({"price":elm.value});
        }
    }

    render(){
        let state = this.state;
        return(
            <section className="ItemEdit"
                style={{border:`1px solid ${state.stateColor}`}}
            >
                <input name="title" type="text" placeholder="Title" value={state.title}
                    onChange={this.updateValues}
                />
                <button className="doneBtn" data-item={JSON.stringify(this.state)}
                    onClick={this.props.updateItem}
                >DONE</button>
                <input name="pic" type="text" placeholder="Url" value={state.pic}
                    onChange={this.updateValues}
                />
                <img src={state.pic} alt={state.title}/>
                <input name="description" type="text" placeholder="Description" value={state.description}
                    onChange={this.updateValues}
                />
                <input name="price" type="text" placeholder="Price" value={state.price}
                       onChange={this.updateValues}
                />
                <input name="amount" type="text" placeholder="Amount" value={state.amount}
                    onChange={this.updateValues}
                />
            </section>
        );
    }
}




