import React, {Component} from "react";
import "../style/historyItem.css";


export default class HistoryItem extends Component{

    render(){
        let type =this.props.item.type;
        let otherCss;
        if(type === "FETCH_PRODUCTS" || type === "FETCH_CART" || type === "ADD_PRODUCT"){
            otherCss = true;
        }

        return(

            <section className="HistoryItem">
                {!otherCss &&
                    <p>{type}</p>
                }
                {otherCss &&
                    <p style={{color:"red"}}>{type}</p>
                }
            </section>
        );
    }
}




