import React, {Component} from "react";
import "../style/Aside.css";
import CartItem from "./CartItem";
import HistoryItem from "./historyItem";


export default class Aside extends Component{

    constructor(props){
        super(props);

        this.state={
            "title":"",
            "pic":"",
            "description":"",
            "amount":"",
            "stateColor":"",
            "price":0
        };

        this.updateValues = this.updateValues.bind(this);
    }

    /*componentWillMount(){
        this.props.updateTotal();
    }*/

    updateValues(e){
        let elm = e.target;

        if(elm.name === "title"){
            this.setState({"title":elm.value});
        }
        else if(elm.name === "pic"){
            this.setState({"pic":elm.value});
        }
        else if(elm.name === "description"){
            this.setState({"description":elm.value});
        }
        else if(elm.name === "amount"){
            this.setState({"amount":elm.value});
        }
        else if(elm.name === "price"){
            this.setState({"price":elm.value});
        }
    }

     
    render(){
        let state = this.state;
        let isAdmin = this.props.isAdmin;
        let cart = this.props.cart;
        let history = this.props.history;
        let toggleHistory = this.props.toggleHistory;
        let btnTxt = "Show History";
        let list = [];
        let historyList = [];
        if(cart !== undefined){
            list = cart.map((item,index)=>{
                return(
                    <li key={index}>
                        <CartItem
                            info={item}
                            removeFromCart={this.props.removeFromCart}
                        />
                    </li>
                );
            });
        }

        if(history !== undefined){
            historyList = history.map((item,index)=>{
                return(
                    <li key={index}>
                        <HistoryItem
                            item={item}
                        />
                    </li>
                );
            });
        }
        if(toggleHistory !== undefined){
            btnTxt = toggleHistory ? "Show Add Form" : "Show History";
        }

        return(
            <section className="Aside">
                {!isAdmin &&
                    <div>
                        <p>Shopping Cart</p>
                        <ul>
                            {list}
                        </ul>
                        To Pay:{this.props.toPay}
                    </div>
                }
                {isAdmin && btnTxt === "Show Add Form" &&
                    <div>
                        <button
                            onClick={this.props.changeViewHistory}
                        >{btnTxt}</button>
                        <p>Session History</p>
                        <ul>
                            {historyList}
                        </ul>
                    </div>
                }
                {isAdmin && btnTxt === "Show History" &&
                    <div>
                        <button
                            onClick={this.props.changeViewHistory}
                        >{btnTxt}</button>
                        <div className="addItem">
                            <input name="title" type="text" placeholder="Title" value={state.title}
                                   onChange={this.updateValues}
                            />
                            <input name="pic" type="text" placeholder="Url" value={state.pic}
                                   onChange={this.updateValues}
                            />
                            <img src={state.pic} alt="Item"/>
                            <input name="description" type="text" placeholder="Description" value={state.description}
                                   onChange={this.updateValues}
                            />
                            <input name="price" type="text" placeholder="Price" value={state.price}
                                   onChange={this.updateValues}
                            />
                            <input name="amount" type="text" placeholder="Amount" value={state.amount}
                                   onChange={this.updateValues}
                            />

                            <button data-item={JSON.stringify(this.state)}
                                onClick={this.props.addItemToFirebase}
                            >ADD</button>
                        </div>
                    </div>
                }
            </section>
        );
    }
}




