import React, { Component } from 'react';
import '../style/App.css';
import Login from "./Login";
import Aside from "./Aside";
import Item from "./Item";
import * as firebase from "firebase";
import actions from "../actions/actions";
import {connect} from "react-redux";

class App extends Component {
    constructor(props){
        super(props);

       this.state = {
            user:{
                uid:"",
                name:"",
                isLoggedIn:false,
                isAdmin: false
            },
            /*products:[],
            history:[],
            cart:[],
            showAside:false,
            showEdit: false,
            toggleHistory:false,
            stateColor:"rgb(19,10,160)",
            toPay:0*/
        };

        this.logIn = this.logIn.bind(this);
        this.logOut = this.logOut.bind(this);
        this.updateUserInput = this.updateUserInput.bind(this);
        this.addToCart = this.addToCart.bind(this);
        this.updateItem = this.updateItem.bind(this);
        this.viewShowEdit = this.viewShowEdit.bind(this);
        this.changeViewHistory = this.changeViewHistory.bind(this);
        this.addItemToFirebase = this.addItemToFirebase.bind(this);
        this.addFirebaseProductToState = this.addFirebaseProductToState.bind(this);
        this.addFirebaseCartToState = this.addFirebaseCartToState.bind(this);
        this.removeFromCart = this.removeFromCart.bind(this);
        this.updateTotal = this.updateTotal.bind(this);
    }

    //Component Methods
    componentDidMount(){
        let user = JSON.parse(localStorage.getItem("user"));
        if(user !== null){
            //SET USER
            //this.setState({user:user});
            let action = actions.setUser(user);
            this.props.dispatch(action);
            this.props.dispatch(actions.addToHistory(action));
        }
        this.addFirebaseProductToState();
    }
    /*componentWillUnmount(){
        this.addFirebaseProductToState();
    }*/
    //--------------------------------
    //Methods

    //Login
    logIn(){

        let newUser = {};
        firebase.auth().signInAnonymously().catch(function(error) {
           console.log(error);
        });

        firebase.auth().onAuthStateChanged((user)=> {
            if (user) {
                //SET USER
                /*this.setState({
                    user:{
                        uid:user.uid,
                        name:this.state.user.name,
                        isLoggedIn:true,
                        isAdmin: this.state.user.name === "Admin"
                    }
                });*/

                newUser = {
                        uid:user.uid,
                        name:this.state.user.name,
                        isLoggedIn:true,
                        isAdmin: this.state.user.name === "Admin"
                };

                //this.setState({user:newUser});
                let action = actions.setUser(newUser);
                this.props.dispatch(action);
                this.props.dispatch(actions.addToHistory(action));
                let storedUser = JSON.stringify(this.props.user);
                localStorage.setItem("user",storedUser);
            }
        });
    }

    //Logout
    logOut(){
        firebase.auth().signOut();
        // SET USER
        /*this.setState({
            user:{
                uid:"",
                name:"",
                isLoggedIn:false,
                isAdmin: false
            }

        });*/
        let newUser = {
            uid:"",
            name:"",
            isLoggedIn:false,
            isAdmin: false
        };

        let action = actions.setUser(newUser);
        this.props.dispatch(action);
        this.props.dispatch(actions.addToHistory(action));
        localStorage.setItem("user",null);
    }

    //Update user Input
    updateUserInput(e){
        let elm = e.target;
        this.setState({
            user:{
                name:elm.value
            }
        });
        this.addFirebaseProductToState();
        this.addFirebaseCartToState();
    }

    //Add To Cart
    addToCart(e){
        let item = JSON.parse(e.target.attributes["data-item"].nodeValue);
        firebase.database().ref("cart/"+item.key).once("value",(snap)=>{
            let data = snap.val();
            if(item.amount > 0){
                if(data !== null){
                    let update = {};
                    item.inCart = data.inCart+1;
                    update[`cart/${item.key}`] = item;
                    firebase.database().ref().update(update);
                }else{
                    item.inCart = item.inCart+1;
                    firebase.database().ref("cart/"+item.key).set(item);
                }
            }

        });

        let update = {};
        item.amount = item.amount > 0 ? item.amount-1 : 0;
        update[`products/${item.key}`] = item;
        firebase.database().ref().update(update);

        this.updateTotal();
        this.addFirebaseProductToState();
        this.addFirebaseCartToState();

        //VIEW ASIDE
        //this.setState({showAside:true});
        let action = actions.viewAside(true);
        this.props.dispatch(action);
        this.props.dispatch(actions.addToHistory(action));
    }

    //Remove From Cart
    removeFromCart(e){
        let cartItem = JSON.parse(e.target.attributes["data-id"].nodeValue);

        firebase.database().ref("products/"+cartItem.key).once("value",(snap)=>{
            let item = snap.val();
            let update = {};
            item.amount = item.amount + cartItem.inCart;
            update[`products/${cartItem.key}`] = item;
            firebase.database().ref().update(update);
        });

        firebase.database().ref(`cart/${cartItem.key}`).remove();
        this.updateTotal();
        let action = {type:"ADD_PRODUCT"};
        this.props.dispatch(actions.addToHistory(action));
        this.addFirebaseCartToState();
    }

    //Update Total
    updateTotal(){
        firebase.database().ref("cart/").on("value",(snap)=>{
            let data = snap.val();
            let total = 0;
            if(data !== null){

                for(let item in data){
                    total += (data[item].price * data[item].inCart);
                }
            }
            // SET_PAY_TOTAL
            //this.setState({toPay:total});
            let action = actions.setPayTotal(total);
            this.props.dispatch(action);
            this.props.dispatch(actions.addToHistory(action));
        });
    }

    //View - Show Edit
    viewShowEdit(e){
        let key = e.target.attributes["data-id"].nodeValue;

        firebase.database().ref("products/"+key).once("value",(snap)=>{
            let data = snap.val();
            data.showEdit = !data.showEdit;
            let update = {};
            update[`products/${key}`] = data;
            firebase.database().ref().update(update);
        });
      //  this.addFirebaseProductToState();
    }

    //Update Item
    updateItem(e){
        let obj = JSON.parse(e.target.attributes["data-item"].nodeValue);
        let update = {};
        obj.showEdit = !obj.showEdit;
        update[`products/${obj.key}`] = obj;
        firebase.database().ref().update(update);
        let action = {type:"EDIT_ITEM"};
        this.props.dispatch(actions.addToHistory(action));
        this.addFirebaseProductToState();
    }

    // view toggle history
    changeViewHistory(){
        //VIEW_TOGGLE_HISTORY
        //this.setState({toggleHistory:!this.state.toggleHistory});
        let action = actions.viewHistory(!this.props.toggleHistory);
        this.props.dispatch(action);
        this.props.dispatch(actions.addToHistory(action));
    }

    //Add Item To Firebase
    addItemToFirebase(e){
        let item = JSON.parse(e.target.attributes["data-item"].nodeValue);
        item.showEdit = false;
        firebase.database().ref("products/").push(item);
        let action = {type:"ADD_PRODUCT"};
        this.props.dispatch(actions.addToHistory(action));
        this.addFirebaseProductToState();

    }

    // Add Firebase Produkct To State
    addFirebaseProductToState(){
        firebase.database().ref("products/").on("value",(snap)=>{
            let data = snap.val();
            let arr = [];
            for(let product in data){
                data[product].key = product;
                arr.push(data[product]);
            }
            //FETCH_PRODUCTS
            //this.setState({products:arr});
            let action = actions.fetchProducts(arr);
            this.props.dispatch(action);
            this.props.dispatch(actions.addToHistory(action));
        });
    }

    //Add Firebase Cart To State
    addFirebaseCartToState(){
        firebase.database().ref("cart/").on("value",(snap)=>{
            let data = snap.val();
            let arr = [];
            for(let cartItem in data){
                arr.push(data[cartItem]);
            }
            //FETCH_CART
            //this.setState({cart:arr});
            let action = actions.fetchCart(arr);
            this.props.dispatch(action);
            this.props.dispatch(actions.addToHistory(action));
        });
    }


    render(){
        let isAdmin = this.props.user.isAdmin;
        let products = this.props.products;
        let itemList = products.map((product,index)=>{
            return(
                <Item
                    user={this.props.user.uid}
                    title={product.title}
                    pic={product.pic}
                    description={product.description}
                    amount={product.amount}
                    stateColor={this.props.stateColor}
                    key={index}
                    index={product.key}
                    isAdmin={this.props.user.isAdmin}
                    addToCart={this.addToCart}
                    showEdit={product.showEdit}
                    viewShowEdit={this.viewShowEdit}
                    updateItem={this.updateItem}
                    price={product.price}
                />
            );
        });

        return(
            <section >
                <div className="Header">
                    <h1>Uppg23Redux</h1>
                    <Login
                        logIn={this.logIn}
                        logOut={this.logOut}
                        isLoggedIn={this.props.user.isLoggedIn}
                        updateUser={this.updateUserInput}
                        nameValue={this.state.user.name}
                        name={this.props.user.name}
                    />
                </div>


                {!isAdmin && this.props.showAside &&
                    <div className="Main">
                    <div className="items">{itemList}</div>
                    <Aside
                    cart={this.props.cart}
                    removeFromCart={this.removeFromCart}
                    toPay = {this.props.toPay}
                    updateTotal={this.updateTotal}
                    />
                    </div>
                }
                {!this.props.showAside && !isAdmin &&
                    <div className="Main">
                        <div className="items">{itemList}</div>
                    </div>
                }
                {isAdmin &&
                    <div className="Main">
                        <div className="items">{itemList}</div>
                        <Aside
                            isAdmin={isAdmin}
                            toggleHistory={this.props.toggleHistory}
                            history={this.props.history}
                            changeViewHistory={this.changeViewHistory}
                            addItemToFirebase={this.addItemToFirebase}

                        />
                    </div>
                }
            </section>
        );
    }
}

function mappStateToProps(state){
    return {
        user:{
            uid:state.user.uid,
            name:state.user.name,
            isLoggedIn:state.user.isLoggedIn,
            isAdmin:state.user.isAdmin
        },
        products:state.products,
        history:state.history,
        cart:state.cart,
        showAside:state.showAside,
        showEdit:state.showEdit,
        toggleHistory:state.toggleHistory,
        stateColor:state.stateColor,
        toPay:state.toPay
    };
}

export default connect(mappStateToProps)(App);



