import TYPE from "../actions/types";

/*
const initialState = {

    user: {
        uid: "",
        name: "",
        isLoggedIn: false,
        isAdmin: false
    },
    products: [],
    history: [],
    cart: [],
    showAside: false,
    showEdit: false,
    toggleHistory: false,
    toPay:0

};
*/

function rootReducer(state, action){
    switch(action.type){
        case TYPE.FETCH_PRODUCTS:
            return {...state, products: action.products};
        case TYPE.FETCH_HISTORY:
            return {...state, history: action.history};
        case TYPE.FETCH_CART:
            return {...state, cart: action.cart};
        case TYPE.SET_USER:
        case TYPE.LOG_IN:
        case TYPE.LOG_OUT:
            return {...state, user: action.user};
        case TYPE.VIEW_ASIDE:
            return {...state, showAside:action.showAside};
        case TYPE.SET_PAY_TOTAL:
            return {...state, toPay:action.toPay};
        case TYPE.ADD_TO_HISTORY:
            return {...state, history:[action.prevAction, ...state.history]};
        case TYPE.VIEW_TOGGLE_HISTORY:
            return {...state, toggleHistory:action.toggleHistory};
        default:
           return state;
    }
}

export default rootReducer;



