import TYPE from "./types";



function viewAside(showAside){
    return{
        type:TYPE.VIEW_ASIDE,
        showAside
    };
}


function setUser(user){
    return{
        type:TYPE.SET_USER,
        user
    };
}


function setPayTotal(toPay){
    return{
        type:TYPE.SET_PAY_TOTAL,
        toPay
    };
}


function fetchProducts(products){
    return{
        type:TYPE.FETCH_PRODUCTS,
        products
    };
}

function fetchCart(cart){
    return{
        type:TYPE.FETCH_CART,
        cart
    };
}


function addToHistory(prevAction){
    return{
        type: TYPE.ADD_TO_HISTORY,
        prevAction
    };
}

function login(user){
    return{
        type:TYPE.LOG_IN,
        user
    };
}

function logOut(){
    return{type:TYPE.LOG_OUT};
}

function actionHistory(history) {
    return {
        type: TYPE.HISTORY,
        history
    };
}

function viewHistory(toggleHistory) {
    return {
        type: TYPE.VIEW_TOGGLE_HISTORY,
        toggleHistory
    };
}

const action = {

    viewAside,
    setUser,
    setPayTotal,
    fetchCart,
    fetchProducts,
    addToHistory,
    login,
    logOut,
    actionHistory,
    viewHistory
};
export default action;






