import React, {Component} from "react";
import "../style/Item.css";
import ItemEdit from "./ItemEdit";

export default class Item extends Component{

    render(){
        let isAdmin = this.props.isAdmin;
        let props = this.props;
        let item = {
            "title":props.title,
            "pic":props.pic,
            "description":props.description,
            "amount":props.amount,
            "key":props.index,
            "inCart":0,
            "showEdit": props.showEdit,
            "price":props.price
        };


        return(
            <section className="Item">

                {!isAdmin && props.user !== "" &&
                    <div style={{
                            border:`none`,
                            padding:"0"
                        }
                    }>
                        <h4>{props.title}</h4>
                        <img src={props.pic} alt="item"/>
                        <p>{props.description}</p>
                        <p>:-{props.price}</p>
                        <button data-item={JSON.stringify(item)} onClick={props.addToCart}>Add to cart</button>
                        <h5>Amount:{props.amount}st</h5>
                    </div>
                }
                {!isAdmin && props.user === "" &&
                    <div style={{
                            border:`none`,
                            padding:"0"
                        }
                    }>
                        <h4>{props.title}</h4>
                        <img src={props.pic} alt="item"/>
                        <p>{props.description}</p>
                        <p>:-{props.price}</p>
                        <h5>Amount:{props.amount}st</h5>
                    </div>
                }
                {isAdmin && props.showEdit &&
                    <ItemEdit item={item} updateItem={props.updateItem}/>
                }
                {isAdmin && !props.showEdit &&
                    <div>
                        <h4>{props.title}</h4>
                        <button data-id={props.index} className="editBtn"
                            onClick={props.viewShowEdit}
                        >EDIT</button>
                        <img src={props.pic} alt="item"/>
                        <p>{props.description}</p>
                        <p>:-{props.price}</p>
                        <button disabled title="Only customers can add items">Add to cart</button>
                        <h5>Amount:{props.amount}</h5>
                    </div>
                }

            </section>
        );
    }
}


