import React, {Component} from "react";
import "../style/Login.css";


export default class Login extends Component{

    render(){
        return(
            <section>

                {!this.props.isLoggedIn &&
                    <div className="Login">
                        <img src="http://aspirehire-co-uk.mysmarterwebsite.co.uk/aspirehire-co-uk/_img/profile.svg" alt="profile" />
                        <input type="text" placeholder="Name" name="name" value={this.props.nameValue}
                            onChange={this.props.updateUser}
                        />
                        <button onClick={this.props.logIn}>Login</button>
                    </div>
                }
                {this.props.isLoggedIn &&
                    <div className="Login">
                        <img src="http://aspirehire-co-uk.mysmarterwebsite.co.uk/aspirehire-co-uk/_img/profile.svg" alt="profile" />
                        <h4 className="welcome" >Welcome {this.props.name}</h4>
                        <button onClick={this.props.logOut}>Log Out</button>
                    </div>
                }
            </section>
        );
    }
}


